.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 5.625rem;
  padding-top: 1.875rem;
}
@media (min-width: 71.875em) {
  .header {
    margin-bottom: 2.6875rem;
  }
}
.header__endState {
  margin-bottom: 5.9375rem;
}
@media (min-width: 71.875em) {
  .header__endState {
    margin-bottom: 4.8125rem;
  }
}

nav {
  width: 100%;
  max-width: 44rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  border: 0.25rem solid var(--header-outline);
  border-radius: 0.3125rem;
  padding-block: 0.625rem;
  padding-inline: 1.25rem;
}
@media (min-width: 71.875em) {
  nav {
    border-radius: 0.9375rem;
    padding-block: 0.9375rem;
  }
}
nav img {
  width: 3.125rem;
  height: 3.125rem;
}
@media (min-width: 40em) {
  nav img {
    width: 5rem;
    height: 5rem;
  }
}
@media (min-width: 71.875em) {
  nav img {
    width: 6.875rem;
    height: 6.875rem;
  }
}
nav h1 {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  border-radius: 0.3125rem;
  padding-block: 0.8125rem;
  padding-inline: 1.25rem;
  background-color: var(--white);
  color: var(--score-text);
  font-size: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
@media (min-width: 40em) {
  nav h1 {
    padding-block: 1.0625rem;
    padding-inline: 2.125rem;
    font-size: 0.625rem;
  }
}
@media (min-width: 71.875em) {
  nav h1 {
    padding-block: 1.25rem;
    padding-inline: 3rem;
    font-size: 0.8125rem;
  }
}
nav h1 span {
  display: block;
  color: var(--dark-text);
  font-size: 1.75rem;
}
@media (min-width: 40em) {
  nav h1 span {
    font-size: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  nav h1 span {
    font-size: 2.8125rem;
  }
}

main {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.start {
  margin-bottom: 7.6875rem;
  width: 100%;
  max-width: 29.6875rem;
  background-image: url("../images/bg-pentagon.svg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: 80% 80%;
}
@media (min-width: 71.875em) {
  .start {
    margin-bottom: 0;
  }
}
.start button {
  position: relative;
  width: 6.25rem;
  height: 6.25rem;
  border-radius: 50%;
  border-width: 0.6875rem;
  border-style: solid;
  padding: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 2.125rem 2.375rem;
  cursor: pointer;
}
@media (min-width: 40em) {
  .start button {
    width: 7.5rem;
    height: 7.5rem;
    border-width: 0.875rem;
  }
}
@media (min-width: 71.875em) {
  .start button {
    width: 9.0625rem;
    height: 9.0625rem;
    border-width: 1.0625rem;
  }
}
.start button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  border-top: 0.3125rem solid hsla(217deg, 16%, 45%, 0.5);
}
.start button:focus {
  transform: translateY(5px);
  box-shadow: none;
}
.start button:focus::before {
  border-top: none;
}
.start__blk1 {
  margin-bottom: 1.5rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-end;
  height: 11.625rem;
}
@media (min-width: 71.875em) {
  .start__blk1 {
    margin-bottom: 2.3125rem;
    height: 17.4375rem;
  }
}
.start__blk1--scissors {
  align-self: flex-start;
  border-color: var(--scissors-grad-to);
  background-image: url("../images/icon-scissors.svg");
  box-shadow: 0 0.3125rem var(--scissors-grad-from);
}
.start__blk1--spock {
  border-color: var(--cyan-to);
  background-image: url("../images/icon-spock.svg");
  box-shadow: 0 0.3125rem var(--cyan-from);
}
.start__blk1--paper {
  border-color: var(--paper-grad-to);
  background-image: url("../images/icon-paper.svg");
  box-shadow: 0 0.3125rem var(--paper-grad-from);
}
.start__blk2 {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
.start__blk2--lizard {
  border-color: var(--lizard-grad-to);
  background-image: url("../images/icon-lizard.svg");
  box-shadow: 0 0.3125rem var(--lizard-grad-from);
}
.start__blk2--rock {
  border-color: var(--rock-grad-to);
  background-image: url("../images/icon-rock.svg");
  box-shadow: 0 0.3125rem var(--rock-grad-from);
}

.end {
  width: 100%;
  max-width: 43.75rem;
  margin-bottom: 3.125rem;
}
@media (min-width: 71.875em) {
  .end {
    max-width: 58.75rem;
    margin-bottom: 1.75rem;
  }
}
.end__blk1 {
  margin-bottom: 5.5rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
@media (max-width: 28.125em) {
  .end__blk1 {
    justify-content: space-between;
  }
}
@media (min-width: 71.875em) {
  .end__blk1 {
    justify-content: space-between;
    margin-bottom: 0;
  }
}
.end__blk1 div {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  gap: 1.6875rem;
  color: var(--white);
  font-size: 0.75rem;
  letter-spacing: 0.125rem;
  text-transform: uppercase;
}
@media (min-width: 71.875em) {
  .end__blk1 div {
    gap: 4.25rem;
    font-size: 1.25rem;
  }
  .end__blk1 div span {
    order: 1;
  }
  .end__blk1 div button {
    order: 2;
  }
}
.end__blk1 button {
  position: relative;
  width: 8.375rem;
  height: 8.375rem;
  border-radius: 50%;
  border-width: 1rem;
  border-style: solid;
  padding: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 3.5rem 3.375rem;
  pointer-events: none;
}
@media (min-width: 40em) {
  .end__blk1 button {
    width: 15.375rem;
    height: 15rem;
    border-width: 1.6875rem;
    background-size: 5.1875rem 6rem;
  }
}
@media (min-width: 71.875em) {
  .end__blk1 button {
    width: 18.5rem;
    height: 18.125rem;
    border-width: 2.3125rem;
    background-size: 6.4375rem 7.25rem;
  }
}
.end__blk1 button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  border-top: 0.3125rem solid hsla(217deg, 16%, 45%, 0.5);
}
@media (min-width: 71.875em) {
  .end__blk1 button::before {
    border-top: 0.625rem solid hsla(217deg, 16%, 45%, 0.5);
  }
}
.end__blk1--picking {
  border-color: var(--radial-grad-from);
  background-color: hsla(237deg, 49%, 15%, 0.5);
}
.end__blk1--picking::before {
  content: "";
  border-top: none !important;
}
.end__blk1--lizard {
  border-color: var(--lizard-grad-to);
  background-image: url("../images/icon-lizard.svg");
  box-shadow: 0 0.625rem var(--lizard-grad-from);
}
.end__blk1--rock {
  border-color: var(--rock-grad-to);
  background-image: url("../images/icon-rock.svg");
  box-shadow: 0 0.625rem var(--rock-grad-from);
}
.end__blk1--scissors {
  border-color: var(--scissors-grad-to);
  background-image: url("../images/icon-scissors.svg");
  box-shadow: 0 0.625rem var(--scissors-grad-from);
}
.end__blk1--spock {
  border-color: var(--cyan-to);
  background-image: url("../images/icon-spock.svg");
  box-shadow: 0 0.625rem var(--cyan-from);
}
.end__blk1--paper {
  border-color: var(--paper-grad-to);
  background-image: url("../images/icon-paper.svg");
  box-shadow: 0 0.625rem var(--paper-grad-from);
}
.end__blk1--result {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 71.8125em) {
  .end__blk1--result {
    display: none;
  }
}
.end__blk1--result h2 {
  margin-bottom: 1.5625rem;
  color: var(--white);
  font-size: 2.5625rem;
  text-transform: uppercase;
}
.end__blk1--result button {
  width: fit-content;
  height: fit-content;
  border: none;
  border-radius: 0.625rem;
  padding-block: 1.1875rem;
  padding-inline: 3.9375rem;
  background-color: var(--white);
  color: var(--radial-grad-to);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  cursor: pointer;
  pointer-events: all;
  transition: opacity 200ms ease-in;
}
.end__blk1--result button::before {
  content: "";
  border: none;
}
.end__blk1--result button:hover {
  opacity: 0.6;
}
.end__blk2 {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  gap: 1.5625rem;
}
@media (min-width: 71.875em) {
  .end__blk2 {
    display: none;
  }
}
.end__blk2 h2 {
  color: var(--white);
  font-size: 2.5rem;
  text-transform: uppercase;
}
.end__blk2 button {
  border: none;
  border-radius: 0.625rem;
  padding-block: 1.1875rem;
  padding-inline: 3.9375rem;
  background-color: var(--white);
  color: var(--radial-grad-to);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  cursor: pointer;
}

.hide {
  display: none;
}

footer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding-bottom: 1.875rem;
}
@media (min-width: 71.875em) {
  footer {
    justify-content: flex-end;
  }
}
footer button {
  border: 0.125rem solid var(--white);
  border-radius: 0.625rem;
  padding-block: 0.875rem;
  padding-inline: 2.375rem;
  background-color: transparent;
  color: var(--white);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.1875rem;
  cursor: pointer;
  transition: all 200ms ease-in;
}
footer button:hover {
  border-color: var(--header-outline);
  color: var(--header-outline);
}

.rules {
  max-width: 100%;
  max-height: 100vh;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  border: none;
  padding-block: 6.25rem;
  padding-inline: 1.875rem;
  background-color: var(--white);
  background-image: url("../images/image-rules-bonus.svg");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center center;
}
@media (min-width: 71.875em) {
  .rules {
    width: 25rem;
    height: 28.75rem;
    border-radius: 0.625rem;
    padding-block: 2.3125rem;
    padding-inline: 2.125rem;
    flex-flow: row wrap;
    align-items: flex-start;
  }
}
.rules::backdrop {
  background-color: hsla(0deg, 0%, 0%, 0.5);
}
.rules h2 {
  color: var(--radial-grad-to);
  font-size: 1.4375rem;
  text-transform: uppercase;
}
.rules button {
  width: 1.4375rem;
  height: 1.4375rem;
  margin: 0;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: url("../images/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  cursor: pointer;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  background-color: var(--radial-grad-from);
}

.fixed {
  overflow: hidden;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --scissors-grad-from: hsl(39, 89%, 29%);
  --scissors-grad-to: hsl(40, 84%, 53%);
  --paper-grad-from: hsl(230, 89%, 42%);
  --paper-grad-to: hsl(230, 89%, 65%);
  --rock-grad-from: hsl(349, 71%, 32%);
  --rock-grad-to: hsl(349, 70%, 56%);
  --lizard-grad-from: hsl(261, 73%, 40%);
  --lizard-grad-to: hsl(261, 72%, 63%);
  --cyan-from: hsl(189, 59%, 33%);
  --cyan-to: hsl(189, 58%, 57%);
  --dark-text: hsl(229, 25%, 31%);
  --score-text: hsl(229, 64%, 46%);
  --header-outline: hsl(217, 16%, 45%);
  --radial-grad-from: hsl(214, 47%, 23%);
  --radial-grad-to: hsl(237, 49%, 15%);
}

:root {
  --font-body: "Barlow Semi Condensed", sans-serif;
}

.container {
  max-width: 81.4375rem;
  margin-inline: 1.875rem;
}
@media (min-width: 87.5em) {
  .container {
    margin-inline: auto;
  }
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}