@use "sass:map";

// 640px, 1150px, 1400px
$breakpoint-up: (
  "medium": 40em,
  "large": 71.875em,
  "xlarge": 87.5em,
);

// 639px, 1149px, 1399px
$breakpoint-down: (
  "xsmall": 28.125em,
  "small": 39.9375em,
  "medium": 71.8125em,
  "large": 87.4375em,
);

@mixin breakpoint-up($size) {
  @media (min-width: map-get($breakpoint-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoint-down, $size)) {
    @content;
  }
}
